
.ImageWithLight {
  position: relative;
}

.Light {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #1C1731;
  background-image: radial-gradient(closest-side, #b95ac3, #1C1731);
  width: 80%;
  height: 100%;
}

.Scattering {
  position: absolute;
  left: 30%;
  top: 15%;
}

.Child {
  position: relative;
}

.Cols {
  @media screen and (max-width: 1200px) {
    flex-direction: column-reverse;
  }
}