
.InfoBlock {
  position: relative;
  margin-bottom: 80px;
  padding: 50px 0;
  overflow: hidden;

  &:last-child {
    margin-bottom: 0;
  }

  @media screen and (max-width: 991px) {
    margin-bottom: 0px;
    padding: 25px 0;
  }
}

.BackgroundImage {
  position: absolute;
  top: 0;
  width: 100%;

  img {
    width: 100%;
    height: 611px;
  }
}

.Content {
  position: relative;
  max-width: 1260px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;

  @media screen and (max-width: 991px) {
    padding: 0 17px;
  }
}

.Cols {
  position: relative;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: 991px) {
    flex-direction: column-reverse !important;
  }
}

.Reverse {
  flex-direction: row-reverse;
}

.ImageCol {
  position: relative;
  display: flex;
  justify-content: center;
  width: 55%;

  @media screen and (max-width: 991px) {
    width: 100%;
  }
}

.BackgroundInfo {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;

  @media screen and (max-width: 991px) {
    display: none;
  }
}

.Counter {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 59px;
  min-height: 59px;
  border: 3px solid #FFFFFF;
  border-radius: 50%;
  font-weight: 800;
  font-size: 24px;
}

.Line {
  width: 100%;
  height: 0.5px;
  background: #fff;
  opacity: 0.5;
}

.TextCol {
  width: 45%;
  padding-top: 60px;

  @media screen and (max-width: 991px) {
    width: 100%;
    margin-bottom: 32px;
    padding-top: 0;
  }
}

.Title {
  width: max-content;
  max-width: 100%;
  margin: 0 auto 80px;
  font-size: 105px;
  font-weight: 800;

  @media screen and (max-width: 1200px) {
    font-size: 64px;
  }

  @media screen and (max-width: 991px) {
    margin-bottom: 20px;
    font-size: 32px;
  }
}

.SecondaryTitle {
  margin-bottom: 24px;
  font-size: 32px;
  line-height: 40px;
  font-weight: 800;

  @media screen and (max-width: 991px) {
    margin-bottom: 12px;
    font-size: 20px;
    line-height: 28px;
  }
}

.Text {
  font-size: 18px;
  font-weight: 400;

  @media screen and (max-width: 991px) {
    font-size: 16px;
  }
}

.Image {
  @media screen and (max-width: 991px) {
    max-height: 523px;
  }
}