.Container {
  display: grid;
  width: 100%;
  min-width: fit-content;
  min-height: 100vh;
  background: #1C1731;
  color: #fff;
}

.Enticing {
  padding: 200px 30px 76px;
  font-size: 46px;
  font-weight: 800;
  text-align: center;
  white-space: break-spaces;

  @media screen and (max-width: 991px) {
    padding: 0 17px 32px;
    font-size: 24px;
    line-height: 32px;
  }
}

.Links {
  display: flex;
  justify-content: center;
  padding-bottom: 76px;
}

.Top {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1260px;
  width: 100%;
  margin: 70px auto 30px;
  padding: 0 30px;

  @media screen and (max-width: 1200px) {
    margin: 32px auto;
    padding: 0 17px;
  }
}

.Languages {
  position: absolute;
  right: 15px;
  display: flex;
  align-items: center;
}

.Language {
  width: 30px;
  height: 20px;
  margin-right: 10px;
  opacity: 0.3;
  cursor: pointer;

  &_selected {
    opacity: 1 !important;
  }

  &:hover {
    transition: 0.3s;
    opacity: 0.8;
  }

  &:last-child {
    margin-right: 0;
  }
}