
.Content {
  position: relative;
  max-width: 1260px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;

  @media screen and (max-width: 1200px) {
    margin-bottom: 40px;
    padding: 0 17px;
  }
}

.TextCol {
  width: 450px;

  @media screen and (max-width: 1200px) {
    width: 100%;
  }
}

.Title {
  margin: 100px 0 20px -14px;
  font-size: 237.058px;
  line-height: 1;
  font-weight: bold;

  @media screen and (max-width: 1200px) {
    margin: 0 0 16px;
    font-size: 48px;
    line-height: 56px;
  }
}

.SecondaryTitle {
  margin-bottom: 80px;
  font-size: 24px;
  line-height: 32px;


  @media screen and (max-width: 1200px) {
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 24px;
  }
}

.Access {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 500;
}

.Cols {
  position: relative;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }
}

.Links {
  display: flex;
}

.Link {
  margin-right: 30px;
  cursor: pointer;

  &:hover {
    transition: 0.3s;
    opacity: 0.7;
  }
}

.ImageCol {
  max-width: 100%;

  @media screen and (max-width: 1200px) {
    margin-left: -30px;
  }
}

.Line {
  width: 100%;
  height: 0.5px;
  margin-bottom: 18px;
  background: #fff;
  opacity: 0.5;
}

.Image {
  @media screen and (max-width: 568px) {
    transform: scale(1.3);
  }
}