
.Footer {
  background: rgba(54, 29, 105, 0.46);
  color: #7C75CA;
}

.Wrapper {
  max-width: 1260px;
  width: 100%;
  padding: 30px;
  margin: 0 auto;
}

.Cols {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: 991px) {
    flex-direction: column;
  }
}

.Col {
  @media screen and (max-width: 991px) {
    &:first-child {
      margin-bottom: 32px;
    }
  }
}

.Link {
  margin-bottom: 16px;
  cursor: pointer;

  &:hover {
    transition: 0.3s;
    opacity: 0.7;
  }
}

.Copyright {
  text-align: center;
  font-size: 18px;
  line-height: 24px;

  @media screen and (max-width: 991px) {
    margin-top: 32px;
  }
}